import * as React from "react";
import { RadialChart } from "react-vis";
import { colors } from "../../../design-system";

interface Props {}

const Income: React.FC<Props> = () => {
  const data = [
    { label: "Female", angle: 62.5 },
    { label: "Male", angle: 37.4 },
    { label: "Other", angle: 0.1 },
  ];

  return (
    <div>
      <p>Income Metrics</p>
      <RadialChart
        colorRange={[colors.greyDark, colors.blueDark, colors.blue]}
        animation
        data={data}
        width={300}
        height={300}
        showLabels
      />
    </div>
  );
};

export default Income;
